<script setup>
import { defineProps } from 'vue'
import { useSetting } from 'store/pinia/setting'

import InputFile from 'components/InputFile.vue'

const props = defineProps({
  currentLogo: {
    type: String,
    default: ''
  },
  loading: {
    type: Boolean,
    default: false
  },
  disabled:{
    type: Boolean,
    default: false
  },
  disabledTooltip: {
    type: String,
    default: ''
  }
})

const store = useSetting()

const onUpload = async (file) => {
  await store.$updateSettingCompanyProfile({
    logo: file,
    is_selected: false,
    _method: 'PUT'
  })
}
</script>

<template>
  <InputFile
    upload-label="Change Company Logo"
    @input="onUpload"
    :file="store.company.logo"
    :loading="store.company.isLoading"
    :disabled="props.disabled"
    :disabled-tooltip="props.disabledTooltip"
  />
</template>
